.App {
  text-align: center;
}
div {
  box-sizing: border-box;
  border-radius: 30px;
  font-family: a고딕10;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}

.spanlist {
  font-weight: bold;
  font-size: large;
  font-family: a고딕10;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #7f98ea;
  text-align: left;
  font-family: a뉴고딕L;
}

.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}

.black-nav .right-align {
  text-align: center;
  padding-right: 10px;
  border : 1px solid gray;
  border-radius: 10px;
  background: #7f98ea;
  color: black;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
